@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 62.5%
}

body {
  margin: 0;
  background: #D3CCE3;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #E9E4F0, #D3CCE3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}


.main-data{margin:150px auto}.main-data .container{max-width:70%;margin:auto;display:grid;grid-template-columns:31% 31% 31%;grid-column-gap:3.5%;grid-row-gap:40px;padding:0 30px;align-items:center}.main-data .container .background-green{background-image:linear-gradient(to right top, #068b29, #0a922a, #0f992a, #14a02b, #19a72b, #1cad31, #1fb337, #21b93d, #22bf49, #24c655, #26cc60, #28d36b)}.main-data .container .background-orange{background-image:linear-gradient(to right top, #cf8e13, #d09018, #d1921d, #d29421, #d39625, #d59828, #d79b2c, #d99d2f, #dca033, #dfa436, #e2a73a, #e5ab3d)}.main-data .container .background-red{background-image:linear-gradient(to right top, #9b0909, #a11312, #a71b19, #ad2221, #b32928, #b9302e, #bf3635, #c53c3b, #cc4443, #d34b4a, #da5352, #e15a5a)}.main-data .container .card-body{box-shadow:0px 0px 5px 3px rgba(0,0,0,0.1);overflow:hidden;text-align:center;border-radius:5%}.main-data .container .card-body-dist{padding:1.5rem;color:black;font-size:2rem;font-family:'Roboto', sans-serif}.main-data .container .card-body-detail{padding:2px}.main-data .container .card-body-detail .sub-detail{margin-bottom:25px}.main-data .container .card-body-detail .sub-detail .title{display:block;margin-bottom:.8px;margin-right:.8px;color:black;font-weight:bold;font-size:1.4rem}.main-data .container .card-body-detail .sub-detail:first-child{padding-top:.8rem}.main-data .container .card-body-detail .sub-detail .val{font-size:1.2rem;color:#fff}@media (max-width: 992px){.main-data .container{max-width:1200px;margin:auto;display:grid;grid-template-columns:48% 48%;grid-column-gap:4%;grid-row-gap:40px;padding:0 30px}}@media (max-width: 600px){.main-data .container{max-width:1200px;margin:auto;display:unset;padding:0 30px}.main-data .container .card-body{width:80%;margin:auto;margin-bottom:40px}}

.reset-btn{position:fixed;bottom:1%;width:13%;transform:translate(-50%, -50%);left:50%;padding:10px;background:transparent}.reset-btn .extra{width:100%;font-size:2rem;background:blue}

.searchbar{box-shadow:0px 5px 5px -2px rgba(0,0,0,0.1);position:fixed;width:100%;background:white;top:0}.searchbar .container{max-width:1300px;margin:auto;padding:20px}.searchbar .container .search-cont{position:relative;width:98%;margin:auto;max-width:600px;border:1px solid #b6b6b6;border-radius:5px;padding:15px 0 15px 10px}.searchbar .container .search-cont .main{display:flex;align-items:center}.searchbar .container .search-cont .main .search-input{border-right:none;border:none;width:90%;font-size:1.4rem}.searchbar .container .search-cont .main .search-icon-cont{width:10%;display:flex;align-items:center;justify-content:center}.searchbar .container .search-cont .results{position:absolute;top:105%;width:100%;box-shadow:0px 4px 5px 3px rgba(0,0,0,0.1);border-radius:5px;overflow:hidden;font-size:2rem;background:#fff;left:0}.searchbar .container .search-cont .results .result{padding:15px 20px;background:#fff;border-bottom:1px solid #ccc;cursor:pointer;display:flex;align-items:center;justify-content:space-between}.searchbar .container .search-cont .results .result .name{font-size:2rem;color:grey;font-weight:bold}.searchbar .container .search-cont .results .result .zone{background-color:green;border-radius:5px;color:#fff;font-size:0.8rem;color:#fff;padding:10px 15px;font-weight:bold;min-width:80px;text-align:center}.searchbar .container .search-cont .results .result:last-child{border-bottom:none}.searchbar .container .search-cont .results .result:hover{background:#f6f6f6}input:focus{outline:none}.highlight-txt{color:#000}@media (max-width: 600px){.topbar .container{padding:15px 10px}.topbar .container .search-cont .main .search-input{width:85%}.topbar .container .search-cont .main .search-icon-cont{width:15%}.main-data{margin:100px 0}.main-data .container .dist{margin-bottom:30px}}

