.searchbar {
    -webkit-box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.1);
    position: fixed;
    width: 100%;
    background: white;
    top: 0;
    .container {
      max-width: 1300px;
      margin: auto;
      padding: 20px;
      .search-cont {
        position: relative;
        width: 98%;
        margin: auto;
        max-width: 600px;
        border: 1px solid rgb(182, 182, 182);
        border-radius: 5px;
        padding: 15px 0 15px 10px;
        .main {
          display: flex;
          align-items: center;
          .search-input {
            border-right: none;
            border: none;
            width: 90%;
            font-size: 1.4rem
          }
          .search-icon-cont {
            width: 10%;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
        .results {
          position: absolute;
          top: 105%;
          width: 100%;
          -webkit-box-shadow: 0px 4px 5px 3px rgba(0, 0, 0, 0.1);
          -moz-box-shadow: 0px 4px 5px 3px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 4px 5px 3px rgba(0, 0, 0, 0.1);
          border-radius: 5px;
          overflow: hidden;
          font-size: 2rem;
          background: #fff;;
          left: 0;
          .result {
            padding: 15px 20px;
            background: #fff;
            border-bottom: 1px solid #ccc;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .name {
              font-size: 2rem;
              color: grey;
              font-weight: bold;
             
            }
            .zone {
              background-color: green;
              border-radius: 5px;
              color: #fff;
              font-size: 0.8rem;
              color: #fff;
              padding: 10px 15px;
              font-weight: bold;
              min-width: 80px;
              text-align: center;
            }
            &:last-child {
              border-bottom: none;
            }
            &:hover {
              background: rgb(246, 246, 246);
            }
          }
        }
      }
    }
  }
  input:focus {
    outline: none;
}
.highlight-txt {
  color: #000;
}


@media (max-width: 600px) {
  .topbar {
      .container {
        padding: 15px 10px;
        .search-cont {
          .main {
            .search-input {
              width: 85%;
            }
            .search-icon-cont {
              width: 15%;
            }
          }
        }
      }
    }
    .main-data {
      margin: 100px 0;
      .container {
        .dist {
          margin-bottom: 30px;
        }
      }
    }
    
   
  }

